<template>
    <div>
        <div class="loginNavFix">
            <img src="../../../assets/login/return.jpg" alt="" class="return" @click="returns">
            <p class="title f30 cfff text-center">全部评论（{{list.length}}）</p>
            <!-- <p class="navBtn f24 cfff pl-30" @click="turnToSend">发布</p> -->
        </div>
        <div class="comm-for" v-if="!($route.query.type == 1 && $route.query.state == 1)">
            <div class="comm-list" v-for="(item,index) in list" :key="index">
                <img class="l" :src="$api.fileUrl + item.headImage" alt="">
                <div class="comm-right l">
                    <div class="comm-title">
                        <div class="comm-name l">{{$route.query.type == 'school' ? item.teacherName : item.teacherName}}</div>
                        <span class="r">{{item.create_time | fileTime(item.create_time)}}</span>
                    </div>
                    
                </div>
                <div class="comm-content">{{item.content}} </div>
            </div>
        </div>
    </div>
</template>

<script>
import {publisFn} from '../../../lib/util'
export default {
  name: 'HelloWorld',
    data () {
        return {
            list:[]
        }
    },
    filters : {
        fileTime(num){
           return publisFn.dateFormat(num).substring(5,publisFn.dateFormat(num).length).replace(/\//g, "-")
        }
    },
    methods : {
       returns(){
            this.$router.go(-1);//返回上一层
        },
        turnToSend(){
            this.$router.push({path:'/setclasscircle'});//跳转发布
        },
        getClassReply(){//班级圈查看评论
            this.$http({url:this.$api.class.getClassReply},{replyTopicId:this.$route.query.id}).then(data => {
                console.log(data);
                if(data.code == 0){
                    this.list = data.data;
                }else{
                    this.$toast(data.msg);
                }
            },err => {
                this.$toast('出错了');
            })
        },
        getSchoolReply(){
            this.$http({url:this.$api.class.getSchoolReply},{schoolTopicId:this.$route.query.id}).then(data => {
                console.log(data);
                if(data.code == 0){
                    this.list = data.data;
                }else{
                    this.$toast(data.msg);
                }
            },err => {
                this.$toast('出错了');
            })
        }
        
    },
    created() {
        if(this.$route.query.type == 'school'){
            this.getSchoolReply();
        }else{
            this.getClassReply();
        }
    },
}
</script>

<style scoped>
.comm-for{padding: 0 0.3rem;position: absolute;top: 0.84rem;right: 0;bottom: 0;left: 0;overflow-y: auto;}
.comm-list{overflow: hidden;border-bottom: 1px solid #f2f2f2;margin-top: 0.3rem;}
.comm-list img{width: 0.7rem;height: 0.7rem;display: block;border-radius: 50%;}
.comm-right{margin-left: 0.3rem;width: 5.8rem;}
.comm-name{font-size: 0.26rem;color: #333333;}
.comm-title{margin-top: 0.2rem;overflow: hidden;}
.comm-content{width: 100%;min-height: 0.49rem;border-radius: 3px;background: #f3f3f3;padding: 0.18rem;box-sizing: border-box;font-size: 0.2rem;line-height: 0.36rem;margin: 0.35rem 0 0.25rem 0;clear: both;}
</style>






